import { Component, OnInit } from '@angular/core';
import { NavController, AlertController, ActionSheetController, LoadingController } from '@ionic/angular';
import { WitePrompt, AppMin } from '../core';
import { Router } from '@angular/router'




@Component({
  selector: 'app-myfollow',
  templateUrl: './myfollow.page.html',
  styleUrls: ['./myfollow.page.scss'],
})
export class MyfollowPage implements OnInit {
  loader;


  //数据
  public objList:[];
  header_class:String="";
  notMoer: boolean ;
  constructor(public navCtrl: NavController, public Router: Router, public appMin: AppMin, public alertCtrl: AlertController, public actionSheetCtrl: ActionSheetController, public loadingCtrl?: LoadingController) {
   
    if (this.appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class_md";
      console.log("判断为安卓系统 设置状态栏样式")
    }

   

  }

  send(value, isshow?: boolean) {

    if (isshow) {
      if (!this.loader) {
        this.loader = this.loadingCtrl.create({
          message: "加载中...",
          duration: 3000

        });
        this.loader.present();
      }

    }



    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.dataHandle(data);
    })


  }

  addFollowList() {
    //加载关注列表
    let obj = {
      type: 'addFollowList',
      userId: this.appMin.getUserid()

    }

    this.send(JSON.stringify(obj), false);

  }
  list_click(val) {
    //列表被单击

    console.log('list_click=' + val);//输出用户ID
    let obj = {
      userId: val,
    }
    //this.navCtrl.push(Consultant, obj);
    this.Router.navigate(['/consultant'], { queryParams: obj })
  }
  dataHandle(meg: any) {
 //   console.log(JSON.stringify(meg));
    if (meg['type'] == "addFollowList_ret") {

      //收到加载数据列表
      this.objList = meg['rows'];
    //  console.log("objList.length=" + this.objList.length)
      if (this.objList.length != 0) {

        this.notMoer = true;
      }

    }
  }
  ngOnInit() {
     this.addFollowList();
  }

}
